import React, { Component } from 'react';
import './App.css';
import logo from './FinalPoster.svg'
import appDemo from './appDemo.mp4'

class BalanceBudget extends Component {
  render() {
    return (
      <div className='App-body'>
        <h1 className='Page-title'>Balance Budget</h1>
        <h2>An App for Managing Finances</h2>
        <div className='Page-content'>
          <p1>"Necessity is the mother of invention" - Plato. When I moved out on my own I looked for an easy to 
              use app that would let me balance my bank account and keep track of what I was spending without
               needing to sync with my online banking or use any kind of fancy pants predictions to learn my 
              spending habits. I just wanted something that would let me manually input an amount, where it was 
              spent and if it came out of a specific budget or not then do some math to keep track of my current 
              balance. When I couldn't find one that worked how I wanted I taught myself Android development and 
              made myself one (and I use it every day).<br></br> <br></br>
              The app has full support for adding multiple accounts, 
              keeping track of transations in those accounts, adding transactions to budgets that show a percentage
              of how much has been used, add a recurring transaction at happens on a specified date every month or 
              on another interval such as weekly, bi-weekly or annually, and customizing the default account and 
              budget for a new transaction to make adding new ones quick and easy.<br></br><br></br>
              It was built using Java in Android Studio and implementing design patterns learned in school.</p1>
        </div>
        <video className='Page-content' src={appDemo} loop autoPlay/>
      </div>
    );
  }
}

export default BalanceBudget;



