import React, { Component } from 'react';
import './App.css';
import logo from './FinalPoster.svg'
import databaseDemo from './DatabaseDemo.mp4'

class Database extends Component {
  render() {
    return (
      <div className='App-body'>
        <h1 className='Page-title'>Workorder Database</h1>
        <h2>MySQL Database with UI Built in Python</h2>
        <div className='Page-content'>
          <p1>This database uses relational database design techniques learned in school to provide
              a simple storage solution for workorder information used in the day to day at my job. It holds
              customer contact information, the date the workorder was created and a description of the reason
              the workorder was created.
              It also provides a place for the technician working on the workorder to add notes and cost items
              so as to give the customer a quote on their final bill.<br></br><br></br>
              The database was built in MySQL and runs on a local server at the company. The user interface was 
              built in Python and was compiled to be depolyed on workstations in the shop.</p1>
        </div>
        <video className='Page-content' src={databaseDemo} loop autoPlay/>
      </div>
    );
  }
}

export default Database;



