import React, { Component } from 'react';
import './App.css';
import poster from './FinalPoster.svg'
import finalPaper from './FinalPaper.pdf'

class ResearchProject extends Component {
  render() {
    return (
      <div className='App-body'>
        <h1 className='Page-title'>Neural Network Initalized with Decision Tree for Document Classification</h1>
        <h2>Abstract</h2>
        <div className='Page-content'>
          <p1>This paper outlines two methods of using machine learning for document classification (Decision Trees and Neural Networks) and proposes an experiment to test the accuracy of a Neural Network Initialized with a Decision Tree (NNIDT) with the results in “Deep Belief Networks for Document Classification” by Justin Rebok [1]. First, TF-IDF and stemming will be described for the information preprocessing stage, then the C4.5 Decision Tree model will be outlined as well as a description of Neural Networks and how to train them using Back Propagation. Third, methodology will be presented for building a NNIDT. The experiment proposed will test three different samples sizes from the BBC dataset using a 90:10 training to testing ratio. First, the NNIDT will be tested without the use of stemming, then stemming will be introduced to see how reduced input affects the accuracy.</p1>
        </div>
        <a href={finalPaper} target="_blank" className='Page-content'>Download Full Paper (PDF)</a>
        <img src={poster} className="Page-content"/>
      </div>
    );
  }
}

export default ResearchProject;



